$primary-color: #17a2b8;
$dark-color: #343a40;
$light-color: #f4f4f4;
$danger-color: #dc3545;
$success-color: #28a745;
$navbarbg: #eeeeee;
$navbar-height: 2.8rem;
$max-width: 1100px;

// Set text color based on background
@function set-text-color($color) {
  @if (lightness($color) > 60) {
    @return #333;
  } @else {
    @return #fff;
  }
}
// set background + text color
@mixin set-background($color) {
  background-color: $color;
  color: set-text-color($color);
}

// Margin & padding
$spacemounts: 1, 2, 3, 4, 5;

@each $space in $spacemounts {
  // all around margin
  .m-#{$space} {
    margin: #{$space}rem;
  }
  // vertical margin
  .my-#{$space} {
    margin: #{$space}rem 0;
  }
  // all around padding
  .p-#{$space} {
    padding: #{$space}rem;
  }
  // vertical padding
  .py-#{$space} {
    padding: #{$space}rem 0;
  }
}
