@import 'config';
@import 'utilities';
@import 'forms';

@mixin debug($color: green) {
  border: 1px solid $color;
}

* {
  box-sizing: border-box; // border-box: padding and border are included in elements widht and height
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway', sans-serif;
  font-size: 1rem; // rem: multiplier of default font size (size of html elementsd default font, usually 16px)
  line-height: 1.6; // adds space above and under the text, not the 2 baselines!
  background-color: #ffffff;
  color: #333;
}

a {
  text-decoration: none;
  color: $primary-color;
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

#page-body {
  margin: 1.2rem;
  margin-top: $navbar-height;
}
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  background-color: $navbarbg;
  height: $navbar-height;
  ul {
    list-style-type: none;
    li {
      padding: 0 20px;
      display: inline-block;
    }
    a {
      text-decoration: none;
      color: black;
      &:hover {
        background-color: black;
        color: white;
      }
    }
  }
}

.logo {
  font-size: 1.4rem;
  font-weight: bold;
  margin: auto 1rem;
}

.article-list {
  ul {
    padding-left: 1rem;
    li {
      border-bottom: 1px solid black;
    }
  }
}

.article {
  margin-left: 0.5rem;
}
